/* purgecss start ignore */

.doctor{
  display:flex;
  width:100%;
  flex-direction:column;
  align-items:center;
  justify-content:space-between;
  gap:1rem
}

@media (min-width: 768px){
  .doctor{
    max-width:36rem
  }
}

@media (min-width: 1024px){
  .doctor{
    max-width:100%;
    flex-direction:row
  }
}

.doctor{
  border-width:1px;
  --tw-border-opacity:1;
  border-color:rgba(156, 163, 175, var(--tw-border-opacity));
  --tw-bg-opacity:1;
  background-color:rgba(249, 250, 251, var(--tw-bg-opacity));
  padding:0.75rem
}

.doctor .info{
  display:flex;
  flex-direction:column;
  align-items:center;
  gap:1rem
}

@media (min-width: 640px){
  .doctor .info{
    flex-direction:row;
    align-items:flex-start
  }
}

.doctor .info .photo-wrap{
  height:150px;
  width:150px;
  flex-shrink:0;
  border-radius:9999px;
  border-width:1px;
  --tw-border-opacity:1;
  border-color:rgba(229, 231, 235, var(--tw-border-opacity));
  padding:0.25rem
}

.doctor .info .photo-wrap .photo{
  height:100%;
  width:100%;
  border-radius:9999px;
  border-width:1px;
  --tw-border-opacity:1;
  border-color:rgba(229, 231, 235, var(--tw-border-opacity));
  background-size:cover;
  background-position:center
}

.doctor .info .text{
  display:flex;
  width:100%;
  flex-direction:column
}

.doctor .info .text .name{
  font-size:1.5rem;
  line-height:2rem;
  --tw-text-opacity:1;
  color:rgba(107, 114, 128, var(--tw-text-opacity))
}

/* purgecss end ignore */